/* General Styling */
body {
    font-family: Arial, sans-serif;
    margin: 0;
    padding: 0;
    color: #333;
  }
  
  .container {
    background-color: #f5f5f5;
  }
  
  /* Navbar Styling */
  .navbar {
    height: 50px;
    background-color: rgba(102, 16, 83, 0.698);
    color: white;
    display: flex;
    align-items: center;
    justify-content: space-around;
  }
  
  .logo {
    font-size: 20px;
    font-weight: bold;
  }
  
  .list {
    display: flex;
    align-items: center;
    list-style: none;
  }
  
  .listItem {
    margin-right: 20px;
    font-weight: 500;
    cursor: pointer;
  }
  
  .avatar {
    width: 32px;
    height: 32px;
    border-radius: 50%;
    object-fit: cover;
  }
  
  /* Home Page Styling */
  .home {
    display: flex;
    justify-content: space-between;
    padding: 50px 100px;
    flex-wrap: wrap;
  }
  
  .card {
    width: 30%;
    padding: 10px;
    box-shadow: 0px 5px 33px -21px rgba(66, 68, 90, 1);
    border-radius: 20px;
  }
  
  .img {
    width: 100%;
    height: 200px;
    object-fit: cover;
    margin: 20px 0;
  }
  
  .desc {
    color: #333;
    margin-bottom: 20px;
    line-height: 25px;
  }
  
  .cardButton {
    border: none;
    border-radius: 10px;
    background-color: rgba(53, 16, 102, 0.678);
    padding: 10px;
    color: white;
    font-weight: bold;
    cursor: pointer;
  }
  
  /* Post Page Styling */
  .post {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  
  .postImg {
    width: 100%;
    height: 300px;
    object-fit: cover;
    margin-bottom: 20px;
  }
  
  .postDesc {
    padding: 0 100px;
    font-size: 24px;
    font-weight: 300;
    color: gray;
    margin-top: 50px;
  }
  
  .postLongDesc {
    padding: 100px;
    padding-top: 50px;
    font-size: 18px;
    line-height: 24px;
  }
  
  /* Login Page Styling */
  .login {
    height: calc(100vh - 50px);
    display: flex;
    align-items: center;
    justify-content: center;
  }
  
  .wrapper {
    width: 60%;
    height: 75%;
    box-shadow: 0px 5px 33px -21px rgba(66, 68, 90, 1);
    display: flex;
    align-items: center;
    border-radius: 20px;
  }
  
  .left,
  .right {
    flex: 1;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  
  .center {
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
  }
  
  .or {
    border: 2px solid lightgray;
    border-radius: 50%;
    padding: 10px;
    color: gray;
    background-color: white;
    font-weight: bold;
  }
  
  .line {
    width: 0.5px;
    height: 70%;
    background-color: lightgray;
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    margin: auto;
    z-index: -1;
  }
  
  .loginButton {
    width: 150px;
    padding: 15px 25px;
    color: white;
    border-radius: 5px;
    display: flex;
    align-items: center;
    font-weight: bold;
    margin-bottom: 20px;
    cursor: pointer;
  }
  
  .google {
    background-color: #df4930;
  }
  
  .facebook {
    background-color: #507cc0;
  }
  
  .github {
    background-color: black;
  }
  
  .icon {
    width: 20%;
    height: 20%;
    margin-right: 10px;
  }
  
  input {
    width: 200px;
    padding: 15px 20px;
    margin-bottom: 20px;
  }
  
  .submit {
    width: 200px;
    background-color: rgba(128, 0, 128, 0.671);
    color: white;
    font-weight: bold;
    border: none;
    padding: 15px 20px;
    border-radius: 5px;
  }
  
  .loginTitle {
    position: absolute;
    top: 150px;
    color: lightgray;
    padding-top: 20px;
  }
  
  .link {
    color: inherit;
    text-decoration: none;
  }
  
  /* Responsive Styling */
  @media screen and (max-width: 992px) {
    .card {
      width: 100%;
      margin-bottom: 50px;
    }
  
    .wrapper {
      width: 65%;
      height: 90%;
      flex-direction: column;
    }
  
    .loginTitle {
      display: none;
    }
  
    .center {
      width: 100%;
      position: relative;
    }
  
    .left {
      padding: 20px;
    }
  
    .right {
      padding: 20px;
    }
  
    .line {
      height: 0.5px;
      width: 300px;
    }
  }
  